import React from "react";
import { Switch, Route } from 'react-router-dom';

import Home from '../pages/Home';
import Moves from '../pages/Moves';
import Pokemon from '../pages/Pokemon';
import { default as Encounters } from '../pages/encounters/Index';
import { default as Rosters } from '../pages/rosters/Index';

function Main(props) {
    return (
      <Switch>
        <Route exact path='/' component={Home}></Route>
        <Route exact path='/moves' component={Moves}></Route>
        <Route exact path='/pokemon' component={Pokemon}></Route>
        <Route path='/routes' component={Encounters}></Route>
        <Route path='/rosters' component={Rosters}></Route>
      </Switch>
    )
}

export default Main;