function Encounters() {
    const areas = Object.keys(AreaComponentMap).map(function(key) {
        return <Route exact path={AreaComponentMap[key].link} component={AreaComponentMap[key].component} /> 
    });

    return (
        <Switch>
            {areas}
        </Switch>
    );
}

function Index(){
    const links = Object.keys(AreaComponentMap).map(function(key) {
        return (
            <MenuItem value={key} to={AreaComponentMap[key].link} component={Link}>
                {AreaComponentMap[key].name}
            </MenuItem>
        )
    });
    return (
        <HashRouter basename="/routes">
            <Select id="area_select" value="Route0">
                <MenuItem value="Route0" to='/' component={Link}>Choose an Area</MenuItem>
                {links}
            </Select>
            <Encounters />
        </HashRouter>
    )
}

export default Index;